.Pre {
  text-align: center;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}

.Pedidos {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  border-radius: 10px 10px 0 0;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.maquinas {
  margin: 2px;
  border: 1px dashed #000;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.maquinas button {
  font-size: 0.9em;
}

.edit {
  margin: 5px;
  border: 2px solid #000;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.edit input,
.edit select {
  width: 33.33%;
  padding: 10px;
  line-height: 1;
  font-size: 0.8rem;
  border: 0;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0;
  background: #fff;
}

.edit .small {
  -ms-flex: 1 1;
      flex: 1 1;
}

.edit .medium {
  -ms-flex: 2 1;
      flex: 2 1;
}

.edit .large {
  -ms-flex: 3 1;
      flex: 3 1;
}

.edit input:focus,
.edit select:focus {
  outline: 0;
  background: #fef2de;
